<!-- This is the toolbar on top -->

<template>
  <v-toolbar app flat>

    <v-container mx-auto py-0>
      <v-layout>
        <v-img
          :src="require('@/assets/ifglogo.jpg')"
          class="mr-5"
          contain
          height="48"
          width="150"
          max-width="150"
          @click="$vuetify.goTo(0)"
        />

        <v-toolbar-items class="hidden-sm-and-down">
          <v-btn v-for="(link, i) in links" :key="i" :to="link.to" flat>{{ link.text }}</v-btn>
        </v-toolbar-items>

        <v-menu class="hidden-md-and-up">
          <v-toolbar-side-icon slot="activator"></v-toolbar-side-icon>
          <v-list>
            <v-list-tile v-for="(link, i) in links" :key="i" :to="link.to">
              <v-list-tile-content>
                <v-list-tile-title>{{ link.text }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-menu>


      </v-layout>
    </v-container>
  </v-toolbar>
</template>

<script>
// Utilities
import { mapGetters, mapMutations } from "vuex";

export default {
  computed: {
    ...mapGetters(["links"])
  },

  methods: {
    ...mapMutations(["toggleDrawer"]),
    onClick(e, item) {
      e.stopPropagation();

      if (item.to || !item.href) return;

      this.$vuetify.goTo(item.href);
    }
  }
};
</script>
